
import {
	Divider,
	ResultPA,
	HeaderPage,
	TableStResPrc,
	UnitValueLand,
	AreasAndAddElms,
	TableHomologationP,
	LandAndConstructions,
	ApproachForm,
} from "Components/Appraisal";

const PhysicalApproach = ({
	delegations : {
		onSubmit,
		hookForm,
		isLoading,
		resultRef,
		unitValueLandRef,
		tableStResPrcRef,
		areasAndAddElmsRef,
		tableHomologationPRef,
		landAndConstructionsRef,
	},
}) => {

	const { isDirty } = hookForm.formState;

	const sections = [
		{
			title : "Investigación de terrenos comparables",
			ref   : unitValueLandRef,
		},
		{
			title : "Procedimiento residual estático",
			ref   : tableStResPrcRef,
		},
		{
			title : "Homologación",
			ref   : tableHomologationPRef,
		},
		{
			title : "Precios de construcción de tipos similares (con base en manuales de costos), Terreno y Construcción",
			ref   : landAndConstructionsRef,
		},
		{
			title : "Áreas Comunes y Elementos Adicionales",
			ref   : areasAndAddElmsRef,
		},
		{
			title : "Resultado del Enfoque Físico",
			ref   : resultRef,
		},
	];

	return (
		<div id="physical" className="section-table">
			<HeaderPage
				isDirty={isDirty}
				currentIndex={[2, 0]}
				section="body"
				onSubmit={onSubmit}
				isSubmitingData={isLoading}
			/>
			<ApproachForm
				id="approach-physcial"
				hookForm={hookForm}
				sections={sections}
				onSubmit={onSubmit}
				sectionTitle="Enfoque Físico"
			>
				<Divider innerRef={unitValueLandRef} title="Investigación de terrenos comparables" />
				<UnitValueLand />
				<Divider innerRef={tableStResPrcRef} title="Procedimiento residual estático" />
				<TableStResPrc />
				<Divider innerRef={tableHomologationPRef} title="Homologación" />
				<TableHomologationP />
				<Divider innerRef={landAndConstructionsRef} title="Precios de construcción de tipos similares (con base en manuales de costos), Terreno y Construcción" />
				<LandAndConstructions  />
				<Divider innerRef={areasAndAddElmsRef} title="Áreas Comunes y Elementos Adicionales" />
				<AreasAndAddElms  />
				<Divider innerRef={resultRef} title="Resultado del Enfoque Físico" />
				<ResultPA />
			</ApproachForm>
		</div>
	);
};

export default PhysicalApproach;
