import { useRef }    from "react";
import { useForm }   from "react-hook-form";
import { useParams } from "react-router-dom";

import { getDefaultArrVals }                        from "Helpers";
import { useHandlerMutation, useValidDateToSubmit } from "Helpers/Hooks";
import { physicalApproachResult }                   from "Helpers/formulas";
import { AppraisalHOCPageFetcher }                  from "Components/Appraisal";

import PhysicalApproach from "./PhysicalApproach";

import {
	PARAMS,
	HOMOLOGATION,
	CONSTRUCTION_PRICE,
	DEFAULT_FETCH_DATA,
	LAND_DEFAULT_VALUE,
	STATISTICS_RES_PROCESS,
	ELEMENTS_DEFAULT_VALUE,
	COMUN_AREAS_DEFAULT_VALUES,
	CONSTRUCTION_DEFAULT_VALUE,
	UNIT_VALULE_LAND_DEFAULT_VALUE,
} from "./PhysicalApproach.constants";


const PhysicalApproachContainer = ({ appraise = DEFAULT_FETCH_DATA }) => {

	const { id : appraiseId } = useParams();

	const unitValueLandRef        = useRef(null);
	const landAndConstructionsRef = useRef(null);
	const areasAndAddElmsRef      = useRef(null);
	const tableStResPrcRef        = useRef(null);
	const tableHomologationPRef   = useRef(null);
	const resultRef               = useRef(null);

	// ------------> RTK-Q <---------------
	const [ editApprasial, { isLoading  : isLoadingMutation } ] = useHandlerMutation({ toastName : "apprasialCRUD" });

	const { physical_approach, page_four } = appraise;

	const {
		land,
		elements,
		comun_areas,
		homologation,
		construction,
		unit_value_land,
		construction_price,
		statistics_res_process,
	 } = physical_approach;

	 const { superficie_total_land, superficie_total_land_subject } = page_four.surface;

	// ------------> hookForm <---------------
	const defaultValues = {
		...physical_approach,
		superficie_total_land_subject,
		surface_f : {
			...physical_approach.surface_f,
			total_land : superficie_total_land,
			lot        : superficie_total_land_subject,
		},
		land_total_surface     : superficie_total_land,
		unit_value_land        : getDefaultArrVals(unit_value_land, UNIT_VALULE_LAND_DEFAULT_VALUE),
		statistics_res_process : getDefaultArrVals(statistics_res_process, STATISTICS_RES_PROCESS),
		homologation           : getDefaultArrVals(homologation, HOMOLOGATION),
		construction_price     : getDefaultArrVals(construction_price, CONSTRUCTION_PRICE),
		land                   : getDefaultArrVals(land, LAND_DEFAULT_VALUE),
		construction           : getDefaultArrVals(construction, CONSTRUCTION_DEFAULT_VALUE),
		comun_areas            : getDefaultArrVals(comun_areas, COMUN_AREAS_DEFAULT_VALUES),
		elements               : getDefaultArrVals(elements, ELEMENTS_DEFAULT_VALUE),
	};

	const hookForm = useForm({ defaultValues });

	const { control, handleSubmit } = hookForm;

	const getValidDateToSubmit = useValidDateToSubmit({ control });

	const getValidSourceToSubmit = (index, source) => {
		const copySource = { ...source };

		const { date } = copySource;
		delete copySource["date"];

		const defaultDate = unit_value_land[index]?.source?.date;
		const dateInptKey = `unit_value_land.${index}.source.date`;

		const validSourceToSubmit = {
			...copySource,
			date : getValidDateToSubmit(date, defaultDate, dateInptKey),
		};
		return validSourceToSubmit;
	};

	const getValidColonyToSubmit = (colony) => {
		delete colony["d_asenta"];
		delete colony["d_tipo_asenta"];
		delete colony["d_estado"];
		delete colony["id_asenta_cpcons"];

		return colony;
	};

	const getValidUbicationToSubmit = (ubication) => {
		const copyUbication = { ...ubication };

		const { colony } = copyUbication;

		const validUbicationToSubmit = {
			...copyUbication,
			colony : getValidColonyToSubmit({ ...colony }),
		};
		return validUbicationToSubmit;
	};

	const buildUnitValueLand = (form_unit_value_land = []) => {
		const dataBuilded = form_unit_value_land.map((element, index) => {
			const source    = getValidSourceToSubmit(index, element.source);
			const ubication = getValidUbicationToSubmit(element.ubication);
			return {
				...element,
				source,
				ubication,
			};
		});
		return dataBuilded;
	};

	const getResultApporach = (vals) => physicalApproachResult(vals);

	const buildDataToSubmit = (formData) => {

		const unit_value_land = buildUnitValueLand(formData.unit_value_land);

		const {
			land_partial_total_val,
			construction_total_val,
			total_val_commun_area,
			total_val_element,
		} = formData;

		const result = getResultApporach([
			land_partial_total_val,
			construction_total_val,
			total_val_commun_area,
			total_val_element,
		]);

		const dataToSubmit = {
			...formData,
			unit_value_land,
			physic_result : result,
		};

		return dataToSubmit;
	};

	const onSubmit =  handleSubmit(async (formData) => {
		const dataToSubmit = buildDataToSubmit(formData);

		await editApprasial({
			module : `/appraisals/physical-approach/${appraiseId}`,
			method : "PUT",
			data   : dataToSubmit,
			tags   : ["appraisals", "apprasial_details"],
		});
	});

	return (
		<PhysicalApproach
			delegations={{
				onSubmit,
				hookForm,
				resultRef,
				unitValueLandRef,
				tableStResPrcRef,
				areasAndAddElmsRef,
				tableHomologationPRef,
				landAndConstructionsRef,
				isLoading : isLoadingMutation,
			}}
		/>
	);
};

export default AppraisalHOCPageFetcher(PhysicalApproachContainer, PARAMS);
